.button-wrapper {
  height: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4em;
  background-color: #e0e1e6;
  color: #333;
  /* Causes all flex items to be same length, regardless of content. If one flex item has a flex of 1 and another has a flex of 2, the item with a flex of 1 will take up 1/3 the space of the one with a flex of 2 */
  flex: 1;
  /* Outline is outside of border */
  border: 0.5px solid #888;
  cursor: pointer;
}

.button-wrapper:hover {
  background-color: #ccced6;
}

.operator {
  background-color: #fe9241;
  color: #fff;
}

.operator:hover {
  background-color: #f38530;
}